import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import Slider from "react-slick";

export default function Partners() {
  const data = useStaticQuery(graphql`
    query {
      brands: allFile(filter: {relativeDirectory: {eq: "partners"}}) {
        edges{
          node{
        childImageSharp {
          gatsbyImageData(placeholder: BLURRED, formats: [AUTO, WEBP, AVIF],height: 115)
          
                
        }
        }
        }
      } 
    }
  `)

  var settings = {
    dots: false,
    infinite: true,
    speed: 1200,
    slidesToShow: 4,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 1200,
    responsive: [
      {
        breakpoint: 0,
        settings: {
          slidesToShow: 2,
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 2,
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 4,
        }
      },
      {
        breakpoint: 1000,
        settings: {
          slidesToShow: 5,
        }
      },
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 5,
        }
      }
    ]
  };

  return (
    <section className="construct-partners-area section_50">
      <div className="container">
        <div className="row">
          <div className="col-md-12">
            <div className="section-heading">
              <h4>clientes</h4>
              <h2>algunos de nuestros clientes</h2>
            </div>
            <Slider {...settings}
              className="partners-slider">
              {
                data.brands.edges.map(({ node }, i) => (
                  <div className="single-partners" key={i}>
                    <GatsbyImage
                      image={getImage(node.childImageSharp)}
                      alt=""
                    />
                  </div>
                ))
              }
            </Slider>
          </div>
        </div>
      </div>
    </section>

  )
}